<template>
  <el-form label-width="80px">
    <el-form-item label="输入旧密码" label-width="120px">
      <span class="r">*</span>
      <el-input placeholder="请输入旧密码" v-model="pwd.oldpass" show-password></el-input>
    </el-form-item>
    <el-form-item label="输入新密码" label-width="120px">
      <span class="r">*</span>
      <el-input placeholder="请输入新密码" v-model="pwd.pass" show-password></el-input>
    </el-form-item>
    <el-form-item label="确认新密码" label-width="120px">
      <span class="r">*</span>
      <el-input placeholder="请再次输入新密码" v-model="pwd.checkpass" show-password></el-input>
    </el-form-item>
    <el-button class="ebt" type="success" @click="onSubmit(pwd)" round>保存</el-button>
  </el-form>
</template>
<script>
import { checkPwd, getSession } from "../network/data";
export default {
  data() {
    return {
      pwd: {
        oldpass: "",
        pass: "",
        checkpass: ""
      }
    };
  },
  created() {
  },
  methods: {
    onSubmit(pwd) {
      checkPwd(pwd).then(res => {
        if (pwd.oldpass == "" || pwd.pass == "" || pwd.checkpass == "")
          this.$message({
          showClose: true,
          message: '请输入密码',
          type: 'error'
        });
        else if (res.err_code === 2) {
          this.$message({
          showClose: true,
          message: "修改失败，" + res.message,  //原密码错误
          type: 'error'
        });
        } else if (pwd.pass !== pwd.checkpass){
          this.$message({
          showClose: true,
          message: '修改失败，两次新密码输入不一致',
          type: 'error'
        });
        }
        else {
          this.$message({
            showClose: true,
            message: '新密码修改成功~',
            type: 'success'
        });
          pwd.oldpass = "";
          pwd.pass = "";
          pwd.checkpass = "";
        }
      });
    }
  }
};
</script>
<style>
.r{
  color: red;
}

.el-input {
  width: 200px;
  margin-left: 10px;
}
.ebt {
  margin-left: 150px;
}
</style>